.bottom-wrapper {
  position: absolute;
  width: 100%;
  height: 52px;
  left: 0px;
  bottom: 0px;
  display: flex;
  background: #3955b4;
  justify-content: center;
  align-items: center;
  border: none;
}

.bottom-wrapper:disabled {
  background: rgba(57, 85, 180, 0.5);
}

.report-icon {
  width: 24px;
  height: 24px;
  margin: 9px;
}

.bottom-button-text {
  font-family: "Noto Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  /* line-height: 140%; */
  /* or 22px */

  text-align: center;
  color: #ffffff;
}
